body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing: border-box;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
}

html{
  margin: 0;
  padding: 0;
}

.App {
  color: bisque;
  font-weight: bold;
  position: absolute;
  height: 100%;
  width: 100%;
  background-image: linear-gradient(to right,
  rgb(234, 181, 107) 10%,
  rgba(164, 96,0,.9) 60%,
  rgba(164, 96,0,.7) 70%,
  rgba(164, 96,0,0) 100%
  ), url(/static/media/grateful.61923bfe.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.wrapper{
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
  width: 100%;
  grid-gap: 2rem;
  gap: 2rem;
  margin: 0rem;
  padding: 0rem;
  background: url(/static/media/grateful.61923bfe.jpg);
  background-position: center;
  background-size: cover;
  color: rgb(184, 122, 46);
  font-weight: bold;
}

.wrapper-grid{
  width: 100%;
  display: grid;
  align-items: center;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
}

.card{
  box-shadow: 0px 2px 8px 0px rgb(216, 184, 147);
  text-align: center;
  position: relative;
  margin: 1.5rem;
  max-height: 100%;
}

.banner-img{
  position: absolute;
  background-image: url(/static/media/grateful.61923bfe.jpg);
  border-radius: .2rem;
  height: 10rem;
  width: 100%;
  background-position: center;
  background-position: no-repeat;
  background-size: cover;
}

.thanks-img{
  width: 8rem;
  -webkit-clip-path: circle(60px at center);
          clip-path: circle(60px at center);
  margin-top: 5rem;
  margin-left: 1.5rem;
}

.description{
  margin: 5rem 1rem;
  font-size: .9rem;
  padding:1.5rem;
}

.btn{
  height: 3rem;
  width: 100%;
}

.header .btn, .service-form .btn {
  height: 2.5rem;
  width: 7rem;
}

.service-form{
  text-align: start;
  font-weight: 400;
  position: relative;
  overflow: hidden;
  margin: .5rem;
  width: 20rem;
  padding: 1rem;
}

.service-p{
  margin-bottom: 6rem;
  text-align: center;
  margin-right: .5rem;
  margin-left: .5rem
}

.App .header{
  margin: 5rem auto;
  text-align: center;
  font-weight: 400;
}

.wrapper .header{
  text-align: center;
}

.footer{
  text-align: center;
}

.bottom{
  bottom: 0;
  position: absolute;
  width: 100%;
}
